"use client"

import { useState, useEffect } from "react"

export default function DiagnosticoSimplesPage() {
  const [logs, setLogs] = useState<string[]>([])
  const [checkboxState, setCheckboxState] = useState({
    checkbox1: true,
    checkbox2: false,
  })

  // Função simples para adicionar logs
  const addLog = (message: string) => {
    setLogs((prev) => [...prev, `${new Date().toISOString().split("T")[1].split(".")[0]}: ${message}`])
  }

  // Efeito para registrar quando o componente é montado
  useEffect(() => {
    addLog("Componente montado com sucesso")
    addLog(`Estado inicial dos checkboxes: ${JSON.stringify(checkboxState)}`)
  }, [])

  // Função para alternar o estado do checkbox
  const toggleCheckbox = (name: string) => {
    addLog(`Alternando checkbox ${name}`)
    setCheckboxState((prev) => ({
      ...prev,
      [name]: !prev[name as keyof typeof prev],
    }))
  }

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-6">Diagnóstico Simples</h1>

      <div className="mb-6 p-4 border rounded-md">
        <h2 className="text-xl font-semibold mb-4">Checkboxes Básicos</h2>

        <div className="space-y-4">
          <div className="flex items-center space-x-2">
            <input
              type="checkbox"
              id="checkbox1"
              checked={checkboxState.checkbox1}
              onChange={() => toggleCheckbox("checkbox1")}
              className="h-4 w-4"
            />
            <label htmlFor="checkbox1">Checkbox 1 (deve estar marcado)</label>
          </div>

          <div className="flex items-center space-x-2">
            <input
              type="checkbox"
              id="checkbox2"
              checked={checkboxState.checkbox2}
              onChange={() => toggleCheckbox("checkbox2")}
              className="h-4 w-4"
            />
            <label htmlFor="checkbox2">Checkbox 2 (deve estar desmarcado)</label>
          </div>
        </div>
      </div>

      <div className="mb-6 p-4 border rounded-md">
        <h2 className="text-xl font-semibold mb-4">Logs</h2>
        <div className="bg-gray-100 p-4 rounded-md">
          <pre className="whitespace-pre-wrap text-sm">
            {logs.map((log, index) => (
              <div key={index}>{log}</div>
            ))}
          </pre>
        </div>
      </div>

      <div className="mb-6 p-4 border rounded-md">
        <h2 className="text-xl font-semibold mb-4">Informações do Ambiente</h2>
        <pre className="bg-gray-100 p-4 rounded-md whitespace-pre-wrap text-sm">
          {`
Navegador: ${typeof window !== "undefined" ? window.navigator.userAgent : "N/A"}
Hora atual: ${new Date().toLocaleString()}
Modo de renderização: Client-side
          `}
        </pre>
      </div>

      <button
        className="px-4 py-2 bg-blue-500 text-white rounded-md"
        onClick={() => {
          addLog("Botão de teste clicado")
          alert("Botão de teste funcionando!")
        }}
      >
        Testar Interatividade
      </button>
    </div>
  )
}
